    #HEADINGS
\*------------------------------------*/
/* stylelint-disable */
h1 {
  font-size: 31.5px;
  line-height: 1.27; }

h2 {
  font-size: 31.5px;
  line-height: 1; }

h3 {
  font-size: 24.5px;
  line-height: 1.27; }

h4 {
